      keyWordSCol: '#fff', //
      oldKeyEle: null, //
      oldPEle: null, //
      Knum: 0, //
      Ktotal: 0, //
      listNumId: 0, //id
      paragraphClickHigh: [], //  dom
      textCenter: '', // ()
      longTextCatalog: [], //
      longTextContent: '', // ()
      centerEle: null, // dom

      queryKeyWord: '', //      
      oldQueryKeyWordReg: '', //
      queryKeyWordArr: [], //
      queryKeyWordReg: '', //      |
      keyWordIndex: [], // 
